import React from 'react'
import './Sanctuary.scss'

import img4 from '../../assets/images/roadmap/cat-left.webp'
import img5 from '../../assets/images/roadmap/4.webp'

function Sanctuary() {

    const navigateTo = (location) => {

        if (window.innerWidth < 1084) {
            openNavBar()
        }

        var height = document.getElementsByClassName("header")[0].offsetHeight;
  
        var container = document.getElementsByClassName('App')[0]
        var offsetTop;

        switch (location) {

        
            case "roadmap":
                offsetTop = document.getElementsByClassName("roadmap")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
        
            default:
                break;
        }

    }




    return (
        <div className='sanctuary'>
            <div className='sanctuary-main'>
                <span className='tc-gold fs-l ffam-regular'>THE SANCTUARY</span>

                <div className='sanctuary-inner'>
                    <p className='tc-white ffam-light fs-s'>
                        Talking about community and building... How about we actually build a REAL Cat Sanctuary together ? It’s always been a dream for us to take care of the least fortunate kitties, and we believe that community driven projects are way more likely to succeed. The Moggie Rescue Club Sanctuary will be a safe and comfortable place for all the cats that we’ll shelter, and there will be no selection !
                        <br/>
                        <br/>
                        We’ll make sure to collaborate with the best vets in towns and will be there at work with other caregivers. We’re planning on a place where all the cats would be able to isolate from the crowd, but cheer up with friends whenever they feel like it. Cats will have access to fresh air and a lot of vegetation, while being in a safe and secure environment. They will also be provided with medical care and nutrition upon any specific need.
                        <br/>
                        <br/>
                        Collection holders will have access to various advantages (virtual and IRL) related to the sanctuary. Many perks will be revealed as the project grows, but we can already tell we’re planning live cams in some areas of the sanctuary for holders only, virtual adoptions, a voting system, high quality merch and even more exclusive advantages.
                    </p>
                    <div className='sanctuary-right'>
                        <img src={img5}/>
                    </div>
                </div>
               
            </div>
        </div>
    )
}

export default Sanctuary