import React, { useRef, useState } from 'react';
import './FaqSection.scss'

// import plusIcn from '../../assets/images/plus.svg'

function Faq({answer,question,discord,discord2}) {

    const faqQuestion=useRef()
    const faqAnswer=useRef()
    const faqAnswerInner=useRef()
    const [faqOpen,setFaqOpen]=useState(false)

    const expand=()=>{
        if(!faqOpen){
            faqQuestion.current.style.borderBottom='2px solid transparent';
            faqAnswer.current.style.height=`${faqAnswerInner.current.offsetHeight}px`;
        }
        else{
            faqQuestion.current.style.borderBottom='2px solid #84834e';
            faqAnswer.current.style.height=`0px`;
        }
        

        setFaqOpen(!faqOpen)
    }
   
    return (
        <div onClick={expand} className='faq'>
            <div ref={faqQuestion} className='faq-question'>
                <span className='fs-s tc-white ffam-medium faq-question-title '>
                    {question}
                </span>
                <span style={{transform:faqOpen?'rotate(495deg)':'rotate(0deg)'}} >+</span>
            </div>
            <div ref={faqAnswer} className='faq-answer'>
                <div ref={faqAnswerInner} className='faq-answer-inner'>
                    <div className='fs-s tc-white ffam-light'>
                       {answer.split('<linkhere>').map((ans,index)=>{
                            if(ans.includes('.eth')){
                                return (<a key={index} href={`https://etherscan.io/enslookup-search?search=${ans}`}>{ans}</a>)
                            }
                            else{
                                return ans.split('<splithere>').map((split,i)=>{
                                    // console.log(split)
                                    if(ans.split('<splithere>').length>1&&i!==ans.split('<splithere>').length-1){
                                        return(
                                            <span  key={i}>
    
                                                <span >{split}</span>
                                                <br/>
                                                <br/>
                                            </span>
                                        )
                                    }
                                    else{
                                        return(
                                            <span  key={i}>
    
                                                <span >{split}</span>
                                            
                                            </span>
                                        ) 
                                    }
                                })
                            }
                       })}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Faq;
