import React, { useEffect, useState } from 'react'
import './MintSection.scss';

import mp4 from '../../assets/images/unknown.mp4'
import webm from '../../assets/images/unknown.webm'
import WalletModal from '../../components/wallet-modal/WalletModal'
import Timer from '../../misc/timer'

import { useDispatch, useSelector } from "react-redux";

import loading from '../../assets/images/loading.svg'
import { toast } from 'react-toastify';

function MintSection() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);

  const [mintAmount, setMintAmount] = useState(1)
  const [limit, setLimit] = useState(50)
  const [open, setOpen] = useState(false)
  const [saleActive, setSaleActive] = useState(null)
  const [dataLoaded, setDataLoaded] = useState(false)
  const [timer, setTimer] = useState(null)
  const [cost, setCost] = useState(null)
  const [claimingNft, setClaimingNft] = useState(null)

  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    GAS_LIMIT: 0,
  });
  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();

  }, []);
  const mint = () => {
    if (claimingNft) return;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);

    setClaimingNft(true);
    toast.info(`Minting your ${mintAmount > 1 ? 'MOGGIE RESCUE CLUB NFTs' : 'MOGGIE RESCUE CLUB NFT'}...`);

    blockchain.smartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        toast.error("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        toast.success(() =>
          <span>
            MOGGIE RESCUE CLUB NFT Mint Successful! Visit <a href="https://opensea.io/">opensea.io</a> to view your NFT.
          </span>
        );
        setClaimingNft(false);
        setMintAmount(1)
      });
  };


  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {

    let newMintAmount = mintAmount + 1;
    if (newMintAmount > limit) {
      newMintAmount = limit;
    }
    if (limit === 0) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const setAmountInput = (amount) => {
    amount = Number(amount)
    // if (isNaN(amount)) return;
    if(amount===0) {
      // console.log(amount)
      setMintAmount(1)
      return
    }
    else if (amount > limit) {
      setMintAmount(limit)
      return
    }
    else {
      setMintAmount(amount)
      return
    }
    // setMintAmount(amount)
  }


  const initConnect = async (connectedAccount) => {
    try {
      const publicSaleCost = await blockchain.smartContract.methods.cost().call();
      const saleActive = await blockchain.smartContract.methods.saleActive().call();
      setCost(publicSaleCost)

      setSaleActive(saleActive)
      setDataLoaded(true)
    }
    catch (err) {
      console.log(err)
    }

  }

  useEffect(() => {
    if (blockchain.smartContract === null) return;

    initConnect(blockchain.account)

    if (timer !== null) {
      timer.stop()
    }

    let timer2 = new Timer(() => {
      initConnect(blockchain.account)
    }, [10000]);

    setTimer(timer2)
  }, [blockchain.account, blockchain.smartContract])

  return (

    <div className='MintSection'>
      <WalletModal open={open} setOpen={setOpen} />
      <div className='mint-section-main'>
        <div className='MintSection-inner'>
          <div className='MintSection-left'>
            <span className='fs-m tc-green ffam-medium '>
              MOGGIE RESCUE CLUB
            </span>
            <br />
            <br />
            {/* <span className="fs-ms tc-white ffam-medium">
              Minting on 11/24/22
            </span>
            <br /> */}
            <div className='MintSection-left-bottom'>
              <div className='MintSection-amount '>


                {
                  blockchain.smartContract === null &&
                  <div className='MintSection-amount-bottom fs-xs tc-white'>
                    <button onClick={() => { setOpen(true) }} className='mint-button'>
                      <span className='fs-s ffam-bold '>
                        Connect Wallet
                      </span>
                    </button>
                  </div>
                }
                {
                  blockchain.smartContract !== null && dataLoaded === false &&
                  <div className='MintSection-amount-bottom fs-xs tc-white'>
                    <button className='mint-button'>
                      <img src={loading}></img>
                    </button>
                  </div>
                }
                {
                  saleActive === false &&
                  <div className='MintSection-amount-bottom fs-xs tc-white'>
                    <button className='mint-button'>
                      <span className='fs-s ffam-bold '>
                        Mint Coming Soon
                      </span>
                    </button>
                  </div>
                }
                {
                  saleActive === true &&
                  <div>

                    <div className='MintSection-amount-top'>
                      <button onClick={() => { decrementMintAmount() }} className='mint-plus-btn   fs-s bold   tc-white'>
                        -
                      </button>
                      <input onChange={(e) => { setAmountInput(e.target.value) }} value={mintAmount} placeholder='1' className='fs-s  tc-white' />
                      <button onClick={() => { incrementMintAmount() }} className='mint-plus-btn  fs-s bold  tc-white'>
                        +
                      </button>
                    </div>

                    <div className='MintSection-amount-bottom fs-xs tc-white'>
                      <button onClick={() => { mint() }} className='mint-button'>
                        {
                          !claimingNft ?
                            <span className='fs-s ffam-bold '>
                              MINT
                            </span>
                            :
                            <img src={loading}></img>
                        }

                      </button>
                    </div>
                  </div>
                }
              </div>

            </div>
          </div>
          <div className='Mint-section-right'>

            <video autoPlay playsInline muted loop>
              <source src={webm}></source>
              <source src={mp4}></source>
            </video>
          </div>
        </div>
      </div>

    </div>
  )
}

export default MintSection
