import React, { useEffect, useState } from 'react'
import './Header.scss'

import headerLogo from '../../assets/images/logo/logo.svg'

import twitterWhite from '../../assets/images/social/twitter.png'
import discordWhite from '../../assets/images/social/discord.png'
import instagramWhite from '../../assets/images/social/instagram.png'
import mediumWhite from '../../assets/images/social/medium.png'
import openseaWhite from '../../assets/images/social/opensea.png'

function Header() {
    const [headerMobileOpen, setHeaderMobileOpen] = useState(false)

    const openLink = (link) => {
        window.open(link)
    }

    const openNavBar = () => {
        let header = document.getElementsByClassName('header')[0]

        let hamTop = document.getElementsByClassName('hamburger-top')[0]
        let hamBottom = document.getElementsByClassName('hamburger-bottom')[0]
        let mobileNav = document.getElementsByClassName('header-mobile-nav')[0]

        if (!headerMobileOpen) {
            hamTop.style.transition = 'all 250ms ease-in-out'
            hamBottom.style.transition = 'all 250ms ease-in-out'
            hamTop.style.top = '19px'
            hamTop.style.transform = 'rotate(45deg)'
            hamBottom.style.bottom = '19px'
            hamBottom.style.transform = 'rotate(-45deg)'

            mobileNav.style.display = 'block'
            setTimeout(() => {
                mobileNav.style.opacity = '1'
            }, [15])
            // header.style.backdropFilter='blur(25px)'
        }
        else {
            hamTop.style.transition = 'transform 100ms ease,bottom 300ms ease-in-out,top 300ms ease-in-out'
            hamBottom.style.transition = 'transform 100ms ease,bottom 300ms ease-in-out,top 300ms ease-in-out'
            hamTop.style.top = '14px'
            hamTop.style.transform = 'rotate(0deg)'
            hamBottom.style.bottom = '14px'
            hamBottom.style.transform = 'rotate(0deg)'

            mobileNav.style.opacity = '0'
            setTimeout(() => {
                mobileNav.style.display = 'none'
            }, [250])
            // header.style.backdropFilter='blur(3px)'

        }
        setHeaderMobileOpen(!headerMobileOpen)
    }


    const navigateTo = (location) => {

        if (window.innerWidth < 1234) {
            openNavBar()
        }

        var height = document.getElementsByClassName("header")[0].offsetHeight;

        
        var container = document.getElementsByClassName('App')[0]
        var offsetTop;

        switch (location) {

            case "mint":
                offsetTop = document.getElementsByClassName("MintSection")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "roadmap":
                offsetTop = document.getElementsByClassName("roadmap")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "about":
                offsetTop = document.getElementsByClassName("about")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "team":
                offsetTop = document.getElementsByClassName("team")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "charity":
                offsetTop = document.getElementsByClassName("charity")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "sanctuary":
                offsetTop = document.getElementsByClassName("sanctuary")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "faq":
                offsetTop = document.getElementsByClassName("faq-section")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            default:
                break;
        }

    }





    const [width, setWidth] = useState(0)

    // useEffect(() => {
    //     window.addEventListener("resize", () => {
    //         setWidth(window.innerWidth)
    //     })
    // }, [])


    return (
        <div className='header '>
            {/* <span className='tc-white'>
                {width}
            </span> */}


            <div className='header-inner'>
                
                <div className='header-left'>
                    <img className='header-logo' src={headerLogo} />
                </div>
                <div className='header-right ffam-light'>
                    <div className='header-links'>
                        {/* <button onClick={()=>navigateTo('mint')} className='header-link tc-white fs-xs'>MINT</button> */}
                        <button onClick={()=>navigateTo('roadmap')} className='header-link tc-white fs-xs'>ROADMAP</button>
                        <button onClick={()=>navigateTo('about')} className='header-link tc-white fs-xs'>ABOUT</button>
                        <button onClick={()=>navigateTo('team')} className='header-link tc-white fs-xs'>TEAM</button>
                        <button onClick={()=>navigateTo('charity')} className='header-link tc-white fs-xs'>CHARITY</button>
                        <button onClick={()=>navigateTo('sanctuary')} className='header-link tc-white fs-xs'>THE SANCTUARY</button>
                        <button onClick={()=>navigateTo('faq')} className='header-link tc-white fs-xs'>FAQ</button>
                    </div>
                    
                    <div className='header-social'>
                        {/* <button  onClick={()=>openLink('https://discord.gg/r4N3kJ49SV')} >
                            <img className='discord-icn' src={discordWhite} />
                        </button> */}
                        <button onClick={()=>openLink('https://twitter.com/moggierescuenft/')}>
                            <img className='twitter-icn' src={twitterWhite} />
                        </button>
                        <button onClick={()=>openLink('https://www.instagram.com/moggierescueclub/')}>
                            <img className='twitter-icn' src={instagramWhite} />
                        </button>
                        <button onClick={()=>openLink('https://opensea.io/collection/moggie-rescue-club')} >
                            <img className='opensea-icn' src={openseaWhite} />
                        </button>
                        <button onClick={()=>openLink('https://moggierescueclub.medium.com/')} >
                            <img className='medium-icn' src={mediumWhite} />
                        </button>
               
                    </div>
                    {/* <button className='header-connect tc-white fs-xs'>
                        CONNECT WALLET
                    </button> */}
                    <button  onClick={()=>{openNavBar()}} className='header-hamburger'>
                        <div className='header-hamburger-inner'>
                            <div className='hamburger-top'></div>
                            <div className='hamburger-bottom'></div>
                        </div>
                    </button>
                </div>


            

                <div className='header-mobile-nav'>
                    <div className='header-mobile-nav-inner '>
                        <img src={headerLogo} className='header-mobile-logo' />
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('mint') }}>MINT</button>
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('roadmap') }}>ROADMAP</button>
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('about') }}>ABOUT</button>
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('team') }}>TEAM</button>
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('charity') }}>CHARITY</button>
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('sanctuary') }}>THE SANCTUARY</button>
                        <button className='fs-s tc-white mobile-nav-button' onClick={() => { navigateTo('faq') }}>FAQ</button>

                        <button onClick={() => openLink('https://discord.gg/r4N3kJ49SV')} className='fs-s  tc-white mobile-nav-button'>
                            DISCORD
                            <img className='header-discord-icn' src={discordWhite}></img>
                        </button>
                        <button onClick={() => openLink('https://twitter.com/moggierescuenft/')} className='fs-s  tc-white mobile-nav-button'>
                            TWITTER
                            <img className='header-twitter-icn' src={twitterWhite}></img>
                        </button>
                        <button onClick={() => openLink('https://www.instagram.com/moggierescueclub/')} className='fs-s  tc-white mobile-nav-button'>
                            INSTAGRAM
                            <img className='header-twitter-icn' src={instagramWhite}></img>
                        </button>
                        <button onClick={() => openLink('https://opensea.io/collection/moggie-rescue-club/')} className='fs-s  tc-white mobile-nav-button'>
                            OPENSEA
                            <img className='header-twitter-icn' src={openseaWhite}></img>
                        </button>
                        <button onClick={() => openLink('https://moggierescueclub.medium.com/')} className='fs-s  tc-white mobile-nav-button'>
                            MEDIUM
                            <img className='header-twitter-icn' src={mediumWhite}></img>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header
